*, *::before, *::after {
  box-sizing: border-box;
}
  
* {
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  height: 100svh;
  color: rgb(204, 204, 204);
  background-color: hsl(0, 0%, 10%);
}
  
main {
  width: min(80ch, 100% - 3rem);
  margin-inline: auto;
}
  
img, picture, svg, video {
  display: block;
  max-width: 100%;
}

form {
  position: relative;
  text-align: center;
  padding-top: 4rem;
}

input {
  text-align: center;
  width: 190px;
  margin-top: 0.5rem;
  margin-inline: auto;
  margin-bottom: 1.5rem;
  font-size: xx-large;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button {  
  opacity: 1;
}

input[type="range"] {
  width: 75%;
}

.gridContainer {
  display: grid;
  grid-template: 1fr / 1fr 1fr;
}

.gridItem {
  position: relative;
  padding-block: 1.25rem;
  border-bottom: 2px solid white;
}

.wide {
  grid-column: 1 /  span 2;
}

.results {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  row-gap: 1rem;
  padding-inline: 0.5rem;
}

.results .center {
  text-align: center;
}

.results h3 {
  text-align: right;
  margin: auto 0;
}

.hidden {
  display: none;
}

.resultTotal {
  grid-column: 1 /  span 3;
}

h1 {
  position: fixed;
  text-align: center;
  letter-spacing: .2rem;
  width: 100%;
  max-width: 100vw;
  max-width: 100svw;
  padding-block: 0.5rem;
  color: rgb(206, 1, 31);
  background-color: hsl(0, 0%, 5%);
  border-bottom: 2px solid hsl(0, 0%, 30%);
  z-index: 10;
}

h2 {
  margin-bottom: 0.5rem;
  letter-spacing: .2rem;
}

h3 {
  letter-spacing: .1rem;
}

h4 {
  font-size: large;
  letter-spacing: .1rem;
}

p {
  font-size: x-large;
  font-weight: 500;
  letter-spacing: .1rem;
}

.ratio p {
  font-size: medium;
}

.email p {
  font-size: medium;
}

input[type="email"] {
  font-size: large;
  width: 75%;
  max-width: 400px;
}

button {
  font-size: x-large;
  padding: 10px 50px;
}

footer p {
  font-size: small;
  text-align: center;
  padding-block: 10px;
}


@media only screen and (max-width: 650px){
  main {
    width: 100vw;
    width: 100svw;
  }

  .gridContainer {
    grid-template: 1fr / 1fr;
  }

  .wide {
    grid-column: 1 /  span 1;
  }    
}